/**
 * 当改变配方或进行其它的一些操作时，会使副产物的生产或需求量发生变化，这时就需要调整副产物的关系网。
 * 但有些副产物和结点硬绑在一起的，例如等离子精炼的副产物'氢'和'X射线裂解'，这样很难对副产物的关系网进行修改。
 * 
 * 戴森球计划让人头疼的是配方有时候会有两种产品，这样对应的结构就不是一棵树，如果用图来表示数据效果也不会太好，
 * 只能用一棵略微奇怪一点的树来表示它，当配方有两种产品时，把其中一种当成是副产品，但这样会就引入了很多的问题。
 * 最开始的时候，数据结构的设计就有问题，我不想改数据结构了，头疼。
 * 
 * 现在一种靠谱的办法就是不修改副产物的关系网，当影响到副产物时，重建整棵树，这样是最简单的。
 * 
//  * 利用副产物的时候，把整个树当成图，以生产副产物的结点为中心，寻找最近的需要这种产品的结点，进行利用。
//  * 如果有多棵树，在两棵树内利用余下的副产物，之前的方法就不适用了，思因为两个棵树之间没有连接，
//  * 副产物还是只在树内利用，其实树内利用完，也不余不下多少副产物。
//  * 树与树之间利用副产物，缺少合理的逻辑，可能会把流图搞乱，只要把没有利用的副产物标出来就行。

处理数据时，所有的速率单位都是：个每秒，显示的时候根据设置进行调整。
 */

import { v4 as uuidv4 } from 'uuid';
import Proliferator, { ProliferatorEffect } from "./Proliferator";
import Facility, { FacilityType } from "./Facility";
import Recipe from "./Recipe";
import Item from './Item';


export interface Link {
  source: string,
  target: string,
  sourceRecipe: string | undefined,
  targetRecipe: string | undefined,
  product: string,
  value: number
}

// objects that save in localstorage.
export interface NodeConfig {
  [key: string]: string | number | boolean
}

export interface NodeConfigs {
  [key: string]: NodeConfig
}


export class Node {

  /**
   * 唯一的标识码。
   * Node.name也就是配方的名称，不唯一，虽然在流图中它看起来是唯一的，但在树中它可能会分布在不同的子树中。
   */
  private _id: string = uuidv4();


  private _recipe?: Recipe;

  private _facility?: Facility;

  private _proliferator: Proliferator = Proliferator.Values[Proliferator.Values.length - 1];

  private _proliferatorEffect = ProliferatorEffect.None;



  /**
   * 子结点，也就是提供生产原料的结点，key是子结点的产品名称。
   */
  private _children: { [key: string]: Node } = {};

  /**
   * 当一个结点使用了‘X射线裂解’生产的氢作为原料，后续又引进副产氢完全替代了原先的氢，
   * 这时不再需要‘X射线裂解’产氢，它的副产“高能石墨”也为零了。
   */

  /**
   * 副产品是否已被使用。
   */
  private _isByProductUsed: boolean = false;

  /**
   * 此结点已使用的副产品。
   * key是副产品的名称，值是生产副产品的结点，
   * 副产品的产量必须小于或等于所需的量，也就是结点生产的副产品要在一个地方用完。
   */
  private _usedByProduct: { [key: string]: Node[] } = {};

  /**
   * 被副产替换掉的子结点。
   */
  private _replacedChild: { [key: string]: Node } = {};

  private _item: Item;
  private _rate: number;
  private _parent?: Node;


  constructor(item: Item, rate: number, parent?: Node) {
    this._item = item;
    this._rate = rate;
    this._parent = parent;
  }


  // getters and setters

  /**
   * 唯一的ID标识。
   */
  public get id(): string {
    return this._id;
  }

  /**
   * 结点的名称，也就是配方的名称，大部分情况下配方和产品同名。
   */
  public get name(): string {
    // if (this.item.recipes.length === 0 || !this.recipe) {
    return this.item.name;
    // } else {
    //   return this.recipe!.name;
    // }
  }

  /**
   * 此结点生产的主产品名称。
   */
  public get product() {
    return this._item.name;
  }

  public get item(): Item {
    return this._item;
  }

  public get parent() {
    return this._parent;
  }

  public set parent(value: Node | undefined) {
    this._parent = value;
  }

  /**
   * 获取根结点。
   * 
   * @returns 根结点
   */
  public getRoot(): Node {
    if (!this.parent) {
      return this;
    }

    return this.parent.getRoot();
  }

  public get proliferator(): Proliferator {
    return this._proliferator;
  }

  public set proliferator(value: Proliferator) {
    // TODO 根据proliferator重新计算children
    this._proliferator = value;
  }

  public get proliferatorEffect(): ProliferatorEffect {
    return this._proliferatorEffect;
  }

  public set proliferatorEffect(value: ProliferatorEffect) {
    if (!this.recipe) {
      return;
    }

    if (!this.recipe.extraProducts && value === ProliferatorEffect.ExtraProducts) {
      return;
    }

    this._proliferatorEffect = value;
  }

  public get numOfProliferators(): number {
    if (this.proliferatorEffect === ProliferatorEffect.None) {
      return 0;
    }

    let materials = this.getMaterials();
    if (!materials) {
      return 0;
    }

    let num = 0;
    for (let rate of Object.values(materials)) {
      num += rate;
    }

    let selfSupply = this.getSelfSupplyMaterials();
    if (selfSupply) {
      num += selfSupply.rate;
    }

    return num / this.proliferator.numOfSprays;
  }


  public setRecipeByName(name: string | undefined) {
    let recipes = this.item.recipes;
    let recipe: Recipe | undefined = undefined;
    for (let i = 0; recipe === undefined && i < recipes.length; i++) {
      if (recipes[i].name === name) {
        recipe = recipes[i];
      }
    }

    return this.setRecipe(recipe);
  }

  public get recipeName(): string | undefined {
    return this.recipe?.name;
  }

  public setRecipe(value: Recipe | undefined) {
    if (!value) {
      return;
    }

    // illegal recipe
    if (!this.item.recipes.includes(value)) {
      return;
    }

    // same recipe
    if (this.recipe === value) {
      return;
    }

    // first assign recipe, don't rebuild
    this._recipe = value;
  }

  public get recipe(): Recipe | undefined {
    return this._recipe;
  }

  public getMaterials(): { [key: string]: number } | undefined {
    if (!this.recipe) {
      return undefined;
    }

    let materials: { [key: string]: number } = {};
    for (let material in this.recipe.materials) {
      let rateOfMaterial = this.recipe.materials[material] / this.recipe.product.quantity * this.rate;
      if (this.proliferatorEffect === ProliferatorEffect.ExtraProducts) {
        rateOfMaterial /= (1 + this.proliferator.extraProducts);
      }
      materials[material] = rateOfMaterial;
    }

    return materials;
  }

  public getSelfSupplyMaterials(): { name: string, rate: number } | undefined {
    if (!this.recipe) {
      return undefined;
    }

    let selfSupply = this.recipe?.selfSupply;
    if (!selfSupply) {
      return undefined;
    }

    let rateOfMaterial = selfSupply.quantity / this.recipe.product.quantity * this.rate;
    if (this.proliferatorEffect === ProliferatorEffect.ExtraProducts) {
      rateOfMaterial /= (1 + this.proliferator.extraProducts);
    }

    return { name: selfSupply.name, rate: rateOfMaterial }
  }

  public get facilityType(): FacilityType | undefined {
    return this.recipe?.madeIn;
  }

  public get facility(): Facility | undefined {
    return this._facility;
  }

  public set facility(value: Facility | undefined) {
    if (this.recipe!.madeIn === value?.type) {
      this._facility = value;
    }
  }

  public countFacilities(): { [key: string]: number } {
    let facilities: { [key: string]: number } = {};
    if (this.facility) {
      facilities[this.facility.name] = this.numOfFacilities;
    }

    for (let child of this.children) {
      let childFacilities = child.countFacilities();
      for (let facilityName in childFacilities) {
        if (facilityName in facilities) {
          facilities[facilityName] += childFacilities[facilityName];
        } else {
          facilities[facilityName] = childFacilities[facilityName];
        }
      }
    }

    for (let facilityName in facilities) {
      facilities[facilityName] = Math.ceil(facilities[facilityName]);
    }
    return facilities;
  }

  public get numOfFacilities(): number {
    let num = 0;
    if (this.recipe) {
      // TODO bug Cannot read properties of undefined (reading 'productionSpeed')
      // 蓄电器（满）
      num = this.rate / (this.recipe.product.quantity / this.recipe.time) / this.facility!.productionSpeed;
      if (this.proliferatorEffect === ProliferatorEffect.ExtraProducts) {
        num /= (1 + this.proliferator.extraProducts);
      } else if (this.proliferatorEffect === ProliferatorEffect.ProductionSpeed) {
        num /= (1 + this.proliferator.productionSpeedup);
      }
    }

    return num;
  }


  public getProductRecipeNames(): { [key: string]: string } {
    let recipeNames: { [key: string]: string } = {};

    if (!this.recipe) {
      return recipeNames;
    }

    recipeNames[this.product] = this.recipe.name;
    for (let child of this.children) {
      Object.assign(recipeNames, child.getProductRecipeNames());
    }

    return recipeNames;
  }


  /**
   * 主产品的生产速率。
   */
  public get rate() {
    return this._rate;
  }

  /**
   * 设置主产品的生产速率，
   */
  public setRate(value: number, nodeConfigs: NodeConfigs = {}) {
    if (value < 0) {
      throw new RangeError(`Node('${this.name}') set rate to ${value}.`);
    }

    this._rate = value;

    if (!this.recipe) {
      // 叶子结点
      return;
    }

    let materials = this.getMaterials();
    for (let material in materials) {
      // children并不一定就会包含所有的material，因为它还有可能使用其它结点的副产物
      // TODO 使用的副产相应的调整
      if (this._children[material]) {
        this._children[material].setRate(materials[material]);
      } else if (!this.recipe.isUnproducibleMaterial(material)) {
        this._children[material] = Trees._Build(material, materials[material], this, nodeConfigs);
      }
    }
  }

  public clearChildren() {
    this._children = {};
  }

  public get children(): Node[] {
    return Object.values(this._children);
  }

  public getChild(material: string): Node | undefined {
    return this._children[material];
  }

  public get producedByProduct(): { name: string, rate: number } | undefined {
    if (!(this.recipe && this.recipe.byProduct)) {
      return undefined;
    }

    return {
      name: this.recipe.byProduct.name,
      rate: this.recipe.byProduct.quantity / this.recipe.product.quantity * this.rate
    }
  }

  public get usedByProduct(): { [key: string]: Node[] } {
    return this._usedByProduct;
  }


  /**
   * 以当前结点为树根，获取树中的所有结点。
   * 
   * @returns 树中的所有结点
   */
  public getNodes(condition: (node: Node) => boolean = (node) => true): Node[] {
    let nodes: Node[] = [];

    if (condition(this)) {
      nodes.push(this);
    }

    for (let child of this.children) {
      nodes.push(...child.getNodes(condition));
    }

    return nodes;
  }

  /**
   * 以当前结点为根，获取整个树的links。
   * 
   * @returns links
   */
  public getLinks(): Link[] {
    let links: Link[] = [];

    if (!this.children) {
      // leaf node
      return links;
    }

    // 获取结点的名称，以配方名为准，如果是叶子结点，以产品名为准

    // 副产品
    for (let product in this.usedByProduct) {
      for (let producer of this.usedByProduct[product]) {
        let link = {
          source: producer.name, //sourceId: producer.id,
          target: this.name, //targetId: this.id,
          sourceRecipe: producer.recipe?.name,
          targetRecipe: this.recipe?.name,
          product: product, value: producer.producedByProduct?.rate!
        };
        links.push(link);
      }
    }

    // 自供：例，X射线裂解，自己消耗自己生产的氢
    // if (root.selfSupply) {
    // TODO sankey do not support circular l ink
    // links.push({ source: getName(root), target: getName(root), product: root.name, value: root.selfSupply });
    // }

    for (let child of this.children) {
      links.push({
        source: child.name,
        sourceRecipe: child.recipe?.name, //sourceId: child.id,
        target: this.name,
        targetRecipe: this.recipe?.name, //targetId: this.id,
        product: child.product, value: child.rate
      });
      links = links.concat(child.getLinks());
    }

    return links;
  }


  /**
   * 以当前结点为树根，获取速查树中所有包含副产物的子结点。
   * 
   * @returns 所有包含副产物的结点
   */
  public getByProductNodes(): Node[] {
    let nodes: Node[] = [];
    if (this.rate === 0) {
      return nodes;
    }

    if (this.recipe?.byProduct) {
      nodes.push(this);
    }

    for (let child of this.children) {
      nodes.push(...child.getByProductNodes());
    }

    return nodes;
  }


  private hasMaterial(material: string): boolean {
    let materials = this.recipe?.materials;
    return materials !== undefined && (material in materials) && (material in this._children);
  }


  /**
   * 获取潜在的可能会消耗当前结点生产的副产物的所有结点，以距离当前结点的远近为序排列，离得越近越靠前。
   * 
   * @returns 潜在的消耗该结点生产的副产物的结点
   */
  public getByProductConsumers(): Node[] {
    if (this.rate === 0) {
      return [];
    }

    // by-product name
    let product = this.recipe?.byProduct?.name;
    if (!product) {
      // 没有副产
      return [];
    }

    // 所有的结点
    let nodes = this.getRoot().getNodes();

    // 计算出当前结点到每一个结点的最近距离，
    // 父结点和子结点一样都算做邻点，整个结点相当于graph，然后用dijkstra算法求解。
    // typescript不能以node为key，这里要用idToNode做转换
    let idToNode: { [key: string]: Node } = {};
    let dist: { [key: string]: number } = {};
    let prev: { [key: string]: string } = {};
    let queue: string[] = [];
    for (let node of nodes) {
      idToNode[node.id] = node;
      dist[node.id] = Number.MAX_VALUE;
      queue.push(node.id);
    }
    dist[this.id] = 0;

    while (queue.length > 0) {
      // find the nearest node
      let [nearestId, nearestIndex] = queue.reduce(
        (nearest, currentId, currentIndex) => {
          let nearestId = nearest[0];
          if (dist[currentId] < dist[nearestId]) {
            return [currentId, currentIndex];
          } else {
            return nearest;
          }
        },
        [queue[0], 0]
      );

      // delete nearest node from queue
      queue.splice(nearestIndex, 1);

      let node = idToNode[nearestId];

      // all neighbour nodes
      let neighbors: Node[] = [];
      if (node.parent) {
        neighbors.push(node.parent);
      }
      if (node.children) {
        neighbors = neighbors.concat(node.children);
      }

      for (let neighbor of neighbors) {
        let alt = dist[node.id] + 1;
        if (alt < dist[neighbor.id]) {
          dist[neighbor.id] = alt;
          prev[neighbor.id] = node.id;
        }
      }
    }


    let consumers: Node[] = nodes.filter(x => x !== this && x.hasMaterial(product!));

    // some node may hate this by-product
    consumers.forEach(x => {
      if (x.recipe!.doYouHateByProduct(product!)) {
        dist[x.id] = Number.MAX_VALUE;
      }
    });

    consumers.sort((a, b) => dist[a.id] - dist[b.id]);

    return consumers;
  }


  public get isByProductUsed(): boolean {
    return this._isByProductUsed;
  }


  private set isByProductUsed(value: boolean) {
    this._isByProductUsed = value;
  }


  public addChild(value: Node) {
    if (value.product in this._children) {
      throw new Error(`${value.name}: A child with the same product already exist.`);
    }
    value.parent = this;
    this._children[value.product] = value;
  }

  public removeChild(value: Node) {
    let product = value.product;
    if (!(product in this._children)) {
      // not exist
      return;
    }

    if (this._children[product] === value) {
      delete this._children[product];
    }
  }


  /**
   * 使用producer的副产品。
   * 
   * precondition: producer必须包含所需的副产品。
   * 
   * @param producer 生产副产品的Node
   * @returns 是否成功使用
   */
  public useByProduct(producer: Node): boolean {
    // TODO 发现bug，宇宙矩阵，X射线裂解的副产品石墨未被完全使用
    if (this.recipe === undefined) {
      return false;
    }
    if (producer.isByProductUsed) {
      return false;
    }

    let product = producer.producedByProduct!.name;
    let productRate = producer.producedByProduct!.rate;
    if (!this._children[product]) {
      // 已经其它副产了
      return false;
    }

    let child = this._children[product];

    if (productRate > child.rate) {
      // 副产品过多，当前结点用不了
      return false;
    }

    if (!(product in this._usedByProduct)) {
      this._usedByProduct[product] = [];
    }

    child.setRate(child.rate - productRate);
    this._usedByProduct[product].push(producer);
    producer.isByProductUsed = true;

    // 当不需要child生产物料时，要删除它
    if (child.rate === 0) {
      this.removeChild(child);
      this._replacedChild[product] = child;
    }

    return true;
  }

}



export default class Trees {

  public static GetNodes(trees: Node[], recipeName: string): Node[] {
    // TODO 无法返回nodes，基本上都是空，可能是配方名称的问题，name或item.name
    let nodes: Node[] = [];
    if (trees.length === 0 || recipeName === undefined) {
      return nodes;
    }

    // if (recipeName.endsWith(' - unproducing')) {
    //   recipeName = recipeName.split(' - ')[0];
    // }

    for (let root of trees) {
      nodes = nodes.concat(root.getNodes((node) => {
        return node.name === recipeName;
      }));
    }

    return nodes;
  }

  public static GetLinks(trees: Node[]): Link[] {
    let links: Link[] = [];

    for (let root of trees) {
      links = links.concat(root.getLinks());
    }

    // merging links
    let linkHelper: { [key: string]: Link } = {};
    links = links.reduce(function (acc: Link[], link: Link) {
      let key = `${link.source} - ${link.target} - ${link.product}`;
      if (!linkHelper[key]) {
        linkHelper[key] = Object.assign({}, link); // a copy of link
        acc.push(linkHelper[key]);
      } else {
        linkHelper[key].value += link.value;
      }
      return acc;
    }, []);

    return links;
  }

  public static CountFacilities(trees: Node[]): { [key: string]: number } {
    let facilities: { [key: string]: number } = {};
    for (let tree of trees) {
      let treeFacilities = tree.countFacilities();
      for (let facilityName in treeFacilities) {
        if (facilityName in facilities) {
          facilities[facilityName] += treeFacilities[facilityName];
        } else {
          facilities[facilityName] = treeFacilities[facilityName];
        }
      }
    }
    return facilities;
  }


  // static methods
  public static _Build(product: string, rate: number, parent: Node | undefined, nodeConfigs: NodeConfigs = {}): Node {

    let item = Item.getItem(product);
    let node = new Node(item, rate, parent);

    // TODO 配置引发的只能采集
    if (item.recipes.length === 0) {
      // TODO 采集相关的facility
      return node;
    }

    let config = nodeConfigs[product];
    if (!config) {
      config = {};
    }

    if (config['gathering']) {
      return node;
    }

    // 父结点已经为当前结点指定了配方
    let recipeName = parent?.recipe?.getMaterialRecipe(product);
    if (recipeName) {
      // let specialItem = Item.getItem(product + "*");
      // for (let specialRecipe of specialItem.recipes) {
      //   if (specialRecipe.name === recipeName) {
      //     console.log(specialRecipe);
      //     node.setRecipe(specialRecipe);
      //     break;
      //   }
      // }
      node.setRecipeByName(recipeName);
    } else if ('recipe' in config) {
      node.setRecipeByName(config['recipe'] as string);
    } else {
      node.setRecipe(node.item.recipes[0]);
    }

    if ('facility' in config) {
      node.facility = Facility.getFacility(config['facility'] as string);
    } else {
      node.facility = Facility.getFacilities(node.recipe!.madeIn)[0];
    }

    if ('proliferator' in config) {
      node.proliferator = Proliferator.Get(config['proliferator'] as string);
    }
    if ('proliferatorEffect' in config) {
      node.proliferatorEffect = config.proliferatorEffect as ProliferatorEffect;
    }

    let materials = node.getMaterials();
    if (!materials) {
      return node;
    }

    for (let [material, materialRate] of Object.entries(materials)) {
      let child: Node;
      if (node.recipe!.isUnproducibleMaterial(material)) {
        // NOTE: X射线裂解需要的原料氢，只能收集（从副产品或其它地方），不能生产，否则会死循环
        child = new Node(Item.getItem(material), materialRate, node);
      } else {
        child = this._Build(material, materialRate, node, nodeConfigs);
      }
      node.addChild(child);
    }

    return node;
  }

  // 在不创建新的Node的情况下，构建树
  public static Build2(node: Node, nodeConfigs: NodeConfigs) {

    let materials = node.getMaterials();
    if (!materials) {
      return node;
    }

    for (let [material, materialRate] of Object.entries(materials)) {
      let child: Node;
      if (node.recipe!.isUnproducibleMaterial(material)) {
        // NOTE: X射线裂解需要的原料氢，只能收集（从副产品或其它地方），不能生产，否则会死循环
        child = new Node(Item.getItem(material), materialRate, node);
      } else {
        child = this._Build(material, materialRate, node);
      }
      node.addChild(child);
    }

    return node;
  }

  /**
   * 首先在同一棵树内使用副产品，之后如果还有余下的副产，再供给其它树。
   */
  public static UseByProducts(root: Node) {
    // group nodes by by-product's name
    let group: { [key: string]: Node[] } = {};
    root.getByProductNodes().reduce((group, current) => {
      let product = current.producedByProduct?.name!;
      if (product in group) {
        group[product].push(current);
      } else {
        group[product] = [current];
      }
      return group;
    }, group);

    // Make sure 'Hydrogen' comes before 'Energetic graphite',
    // bacause use by-product 'Hydrogen' will affect the production rate of by-product 'Energetic graphite'.
    let products = Object.keys(group).sort().reverse();
    for (let product of products) {
      let producers = group[product];
      // sort by by-product's rate
      // producers.sort((a, b) => b.producedByProduct?.rate! - a.producedByProduct?.rate!);
      for (let node of producers) {
        for (let consumer of node.getByProductConsumers()) {
          if (consumer.useByProduct(node)) {
            break;
          }
        }
      }
    }
  }

  public static GetProductRecipeNames(trees: Node[]): { [key: string]: string } {
    let recipeNames: { [key: string]: string } = {};
    for (let tree of trees) {
      Object.assign(recipeNames, tree.getProductRecipeNames());
    }
    return recipeNames;
  }

  public static Build(product: string, nodeConfigs: NodeConfigs) {
    let rate = 1;
    if (product in nodeConfigs && nodeConfigs[product].rate !== undefined) {
      rate = nodeConfigs[product].rate as number;
    }

    let root = this._Build(product, rate, undefined, nodeConfigs);
    this.UseByProducts(root);

    return root;
  }


}