import data from "./data.json";


enum Effect {
  None = "No Proliferator",
  ExtraProducts = "Extra Products",
  ProductionSpeed = "Production Speed"
}

export { Effect as ProliferatorEffect };


export default class Proliferator {
  private constructor(
    private _name: string,
    private _numOfSprays: number,
    private _extraProducts: number,
    private _productionSpeedup: number,
    private _energyConsumption: number
  ) { }

  public get name(): string {
    return this._name;
  }

  public get numOfSprays(): number {
    return this._numOfSprays;
  }

  public get extraProducts(): number {
    return this._extraProducts;
  }

  public get productionSpeedup(): number {
    return this._productionSpeedup;
  }

  public get energyConsumption(): number {
    return this._energyConsumption;
  }

  public static Get(name: string): Proliferator {
    return this.Values.filter((x) => x.name === name)[0];
  }

  public static readonly Values = data.proliferator.map(
    (value) => {
      return new Proliferator(
        value.name,
        value.numOfSprays,
        value.extraProducts,
        value.productionSpeedup,
        value.energyConsumption
      );
    }
  );
}
