import React from 'react';
import Select, { components } from 'react-select';
import './Select.css';
import i18n from '../i18n';
import { withTranslation } from 'react-i18next';

/**
 * 此处使用了第三方类库react-select，类库的具体的细节搞清楚需要时间，这里不再细究了，
 * 因此此处的代码会有一些乱，也就这样吧，以后少用此类第三方类库。
 */


// 选项图标
const Icon = (props) => {
  let value = props.data.value;
  if(value.endsWith('*')) {
    value = value.slice(0, -1);
  }
  return (
    <span>
      {props.data.value &&
        <img className='react-select-avatar' src={`icons/${value}.png`} />
      }
    </span>
  )
};

const TextLabel = (props) => (
  <label>{i18n.t(props.data.value)}</label>
);

// 列表项的图标和文字
const BaseOption = (props) => (
  <components.Option {...props}>
    {props.hasIcon &&
      <Icon {...props} />
    }
    {props.hasText &&
      <span className='react-select-option-label'>{i18n.t(props.data.value)}</span>
    }
  </components.Option>
);

// NOTE hasText做为Select的属性，无法传递到Option，传递到SelectContainer组件就断掉了，只能用下面的这种方法
const Option = (props) => <BaseOption {...props} hasText={true} hasIcon={true} />
const OptionNoText = (props) => <BaseOption {...props} hasText={false} hasIcon={true} />
const OptionNoIcon = (props) => <BaseOption {...props} hasText={true} hasIcon={false} />


const SingleValue = ({ children, ...props }) => (
  <components.SingleValue {...props}>
    <Icon {...props} />
    {/* {children} */}
  </components.SingleValue>
);


class CustomSelect extends React.Component {

  render() {
    let isDisabled = this.props.options.length <= 1;
    let option = this.props.onlyText ? OptionNoIcon : (this.props.onlyIcon ? OptionNoText : Option);

    let props = this.props.isMulti ? (
      {
        isSearchable: true,
        closeMenuOnSelect: false,
        components: {
          Option: option,
          MultiValueLabel: Icon,
          // ClearIndicator: () => null,
          // DropdownIndicator: () => null,
          // IndicatorSeparator: () => null
        },
      }
    ) : (
      {
        isMulti: false,
        isSearchable: false,
        closeMenuOnSelect: true,
        components: {
          Option: option,
          SingleValue: SingleValue,
          DropdownIndicator: () => null,
          IndicatorSeparator: () => null
        }
      }
    );

    if (this.props.onlyText) {
      return (
        <Select {...this.props} isDisabled={isDisabled}
          components={{ DropdownIndicator: () => null, IndicatorSeparator: () => null }} />
      );
    } else {
      return (<Select {...this.props} {...props} isDisabled={isDisabled} />);
    }
  }
}

CustomSelect.defaultProps = {
  onlyIcon: false,
  onlyText: false,
  isMulti: false,
}


export default withTranslation()(CustomSelect);
