import React, { ReactElement } from "react";
import { withTranslation, WithTranslation } from "react-i18next";
import { Node } from "../model/Trees";
import ItemRate from "./ItemRate";


/**
 * 将一个大的数组分割成若干个固定大小的小数组。
 * 
 * @param array 
 * @param n 子数组的大小
 */
export function divideIntoGroups<T>(array: T[], n: number) {
  let result: T[][] = [[]];

  array.reduce((groups: T[][], value: T) => {
    let last = groups[groups.length - 1];
    if (last.length >= n) {
      last = [];
      groups.push(last);
    }
    last.push(value);
    return groups;
  }, result);

  return result;
}

interface Props extends WithTranslation {
  numOfRows: number,
  numOfCols: number,
  trees: Node[],
  perSecond: boolean,
  onRateChanged: (item: string, rate: number) => void
}


class RateEditor extends React.Component<Props> {
  static defaultProps = {
    numOfRows: 5,
    numOfCols: 2
  }

  private pageElementRefs: React.RefObject<HTMLDivElement>[] = [];

  constructor(props: Props) {
    super(props);
  }

  render() {
    // group
    // let numOfRows = 5;
    // let numOfCols = 3;
    let trees = divideIntoGroups(divideIntoGroups(this.props.trees, 2), this.props.numOfRows);
    // this.pageElementRefs = [];
    // pageElementRefs 每render一次，就会往它里面添加一定数量的refs,
    // 但如果要把创建ref放到constructor，更新props.trees时，pageElements又不会更新
    for (let i = 0; i < trees.length; i++) {
      this.pageElementRefs.push(React.createRef<HTMLDivElement>());
    }

    // console.log(this.trees.flat(2).length);
    // console.log(`${this.pageElementRefs.length} ${this.props.trees.length}`);

    // Trees -> html elements
    let pages: ReactElement<HTMLDivElement>[] = [];
    for (let [i, page] of trees.entries()) {
      let rows: ReactElement<HTMLDivElement>[] = [];
      for (let [j, row] of page.entries()) {
        let cols: ReactElement<HTMLDivElement>[] = [];
        for (let [k, tree] of row.entries()) {
          cols.push(
            <div key={k} className="col">
              <ItemRate options={[{ value: tree.item.name }]} item={{ value: tree.item.name }} editable={true}
                rate={this.props.perSecond ? tree.rate : tree.rate * 60}
                onRateChanged={(rate: number) => this.handleRateChange(tree.item.name, this.props.perSecond ? rate : rate / 60)}
                decimalPlaces={10}
              />
            </div>
          );
        }
        rows.push(<div key={j} className="row">{cols}</div>);
      }
      let pageClassName = 'carousel-item';
      if (i === 0) {
        pageClassName += ' active';
      }
      pages.push(<div ref={this.pageElementRefs[i]} key={i} className={pageClassName}>{rows}</div>);
    }

    // show pagination when more than one page
    let pagination: ReactElement<HTMLDivElement> | undefined = undefined;
    if (pages.length > 1) {
      let pageItems: ReactElement<HTMLLIElement>[] = [];
      for (let i = 0; i < pages.length; i++) {
        pageItems.push(<li key={i} className="page-item"><a className="page-link" href="#" onClick={(e) => {
          // clear other active
          for (let ref of this.pageElementRefs) {
            if (ref.current) {
              ref.current.className = "carousel-item";
            }
          }
          // set active
          if (this.pageElementRefs[i].current) {
            this.pageElementRefs[i].current!.className = "carousel-item active";
          }
        }}><small>{i + 1}</small></a></li>);
      }
      pagination = (
        <nav>
          <ul className="pagination pagination-sm justify-content-center">
            {pageItems}
          </ul>
        </nav>
      );
    }

    return (
      <div className="mb-1">
        <div id="carouselSlides" className="carousel slide mb-1" data-bs-ride="carousel" data-bs-interval="false">
          <div className="carousel-inner">
            {pages}
          </div>
        </div>
        {pagination}
      </div>
    );
  }

  handleRateChange = (itemName: string, rate: number) => {
    this.props.onRateChanged(itemName, rate);
  }

}


export default withTranslation()(RateEditor);