import React from "react";
import { withTranslation, WithTranslation } from "react-i18next";
import SankeyChart from "./FlowChart.d3";
import Trees, { Node } from "../model/Trees";

interface Props extends WithTranslation {
  width: number,
  height: number,
  trees: Node[],
  perSecond: boolean,
  onNodeClicked: (name: string) => void,
  selectedNode?: string,
}

class FlowChart extends React.Component<Props> {
  private ref: React.RefObject<SVGSVGElement> = React.createRef();

  componentDidMount() {
    SankeyChart(
      this.ref.current, Trees.GetLinks(this.props.trees), this.props.onNodeClicked,
      this.props.width, this.props.height,
      this.props.selectedNode,
      this.props.perSecond
    );
  }

  componentDidUpdate() {
    let links = Trees.GetLinks(this.props.trees);
    SankeyChart(
      this.ref.current, links, this.props.onNodeClicked,
      this.props.width, this.props.height,
      this.props.selectedNode,
      this.props.perSecond
    );
  }

  render() {
    return (
      <div>
        <svg ref={this.ref}>
        </svg>
      </div>
    );
  }
}

export default withTranslation()(FlowChart);