import data from "./data.json";
import Recipe from "./Recipe";
import { FacilityType } from "./Facility";


export default class Item {

  private constructor(
    private _name: string,
    private _recipes: Recipe[],
    private _gatheredBy: FacilityType[] = [],
    private _gatheredFrom: string[] = [],
  ) { }


  public get name() {
    return this._name;
  }

  public get recipes() {
    return this._recipes;
  }

  public get gatheredBy() {
    return this._gatheredBy;
  }

  public get gatheredFrom() {
    return this._gatheredFrom;
  }

  private static InitItems() {
    let items: { [key: string]: Item } = {};
    // 初始化数据
    data.item.forEach(itemJsonObj => {
      let itemName: string = itemJsonObj['name'];

      let gatheredBy: FacilityType[] = [];
      if (itemJsonObj.hasOwnProperty('gatheredBy')) {
        gatheredBy = itemJsonObj['gatheredBy'] as FacilityType[];
      }

      let gatheredFrom: string[] = [];
      if (itemJsonObj.hasOwnProperty('gatheredFrom')) {
        (itemJsonObj['gatheredFrom'] as string[]).forEach(planet => {
          gatheredFrom.push(planet);
        })
      }

      let recipes: Recipe[] = [];
      if (itemJsonObj.hasOwnProperty('recipe')) {
        (itemJsonObj['recipe'] as []).forEach((recipeJsonObj: any) => {
          let name: string = recipeJsonObj['name'];
          let madeIn: FacilityType = recipeJsonObj['madeIn'];
          let materials: { [key: string]: number } = recipeJsonObj['materials'];
          let time: number = recipeJsonObj['time'];
          let product: { name: string, quantity: number } = recipeJsonObj['product'];

          let byProduct: { name: string, quantity: number } | undefined = undefined;
          if (recipeJsonObj.hasOwnProperty('byProduct')) {
            byProduct = recipeJsonObj['byProduct'];
          }

          let extraProducts = true;
          if (recipeJsonObj.hasOwnProperty('extraProducts')) {
            extraProducts = recipeJsonObj['extraProducts'];
          }

          let selfSupply: { name: string, quantity: number } | undefined = undefined;
          if (recipeJsonObj.hasOwnProperty('selfSupply')) {
            selfSupply = recipeJsonObj['selfSupply'];
          }

          let unproducibleMaterials: string[] = [];
          if (recipeJsonObj.hasOwnProperty('unproducibleMaterials')) {
            unproducibleMaterials = recipeJsonObj['unproducibleMaterials'];
          }

          let hatedByProducts: string[] = [];
          if (recipeJsonObj.hasOwnProperty('hatedByProducts')) {
            hatedByProducts = recipeJsonObj['hatedByProducts'];
          }

          let materialRecipe: { [key: string]: string } = {};
          if (recipeJsonObj.hasOwnProperty('materialRecipe')) {
            materialRecipe = recipeJsonObj['materialRecipe'];
          }

          recipes.push(new Recipe(
            name, madeIn, materials, time, product,
            byProduct, extraProducts, selfSupply,
            unproducibleMaterials, hatedByProducts,
            materialRecipe
          ));
        })
      }

      items[itemName] = new Item(itemName, recipes, gatheredBy, gatheredFrom);
    });

    return items;
  }

  private static ITEMS: { [key: string]: Item } = this.InitItems();

  public static getItem(name: string): Item {
    if (!(name in this.ITEMS)) {
      throw new Error(`item '${name}' not exist.`);
    }
    return this.ITEMS[name];
  }

  public static getItemByRecipeName(recipeName: string): Item | undefined {
    for (let item of Object.values(this.ITEMS)) {
      for (let recipe of item.recipes) {
        if (recipe.name === recipeName) {
          return item;
        }
      }
    }

    return undefined;
  }
}