import React, { ReactElement } from "react";
import { withTranslation, WithTranslation } from "react-i18next";
import Select, { MenuPlacement } from "react-select";
import CustomSelect from "./Select";
import "./ItemRate.css";
import { format, addLabelToOption, addLabelToOptions } from "../util/util";



interface Props extends WithTranslation {
  menuPlacement: string,

  options: { value: string }[],
  item: { value: string },
  onlyText: boolean,

  rate: number,
  denominator: number,
  editable: boolean,
  decimalPlaces: number,

  onItemChanged?: (item: string) => void,
  onRateChanged?: (rate: number) => void,
}




class ItemRate extends React.Component<Props> {
  render() {
    return (
      <div className='input-group input-group-sm mb-1'>
        <CustomSelect className="react-select-start" classNamePrefix="react-select-start"
          menuPlacement={this.props.menuPlacement} onlyText={this.props.onlyText} onlyIcon={true}
          options={addLabelToOptions(this.props.options)} value={addLabelToOption(this.props.item)}
          onChange={(option: { value: string }) => { this.props.onItemChanged?.(option.value) }}
        />

        {this.props.denominator > 0 &&
          <input className="form-control text-center" type="text" readOnly={true}
            value={`${format(this.props.rate / this.props.denominator, this.props.decimalPlaces)} * ${format(this.props.denominator, 1)}`}
          />
        }

        <input className="form-control text-center" readOnly={!this.props.editable} type="number"
          value={format(this.props.rate, this.props.decimalPlaces)} onChange={(e) => { this.props.onRateChanged?.(Number(e.target.value)) }}
        />
      </div>
    );
  }


  static defaultProps: Partial<Props> = {
    menuPlacement: "auto",
    editable: false,
    denominator: 0,
    onlyText: false,
    decimalPlaces: 3,
  }
}


export default withTranslation()(ItemRate);
