import React from "react";
import { WithTranslation } from 'react-i18next';
import * as LocalStorage from '../util/LocalStorage'


interface Props extends WithTranslation {
}

interface State {
  [key: string]: number
};


export default class DysonSphere extends React.Component<Props, State> {

  private static readonly DATA_KEY = "dysonsphere";
  state: State;

  constructor(props: Props) {
    super(props);
    // for test
    // window.localStorage.clear();

    this.state = {
      starLumination: 1,

      // dyson shell
      solarSailProduction: 0,
      // default per second
      solarSailProductionTimeUnit: 1,
      solarSailLife: 3600,

      // dyson swarm
      structurePoint: 0,
      cellPoint: 0,
      nodes: 0,
      adsorptionTimeUnit: 1,
    };

    let data = LocalStorage.LoadData(DysonSphere.DATA_KEY);
    if (data === undefined) {
      data = {};
    }
    for (let key in this.state) {
      if (data.hasOwnProperty(key)) {
        this.state[key] = parseFloat(data[key]);
      }
    }

    this.handleChange = this.handleChange.bind(this);
  }

  componentDidUpdate(_prevProps: Props, prevState: State) {
    if (this.state !== prevState) {
      LocalStorage.SaveData(DysonSphere.DATA_KEY, this.state);
    }
  }

  handleChange(event: any) {
    this.setState<never>({
      [event.target.name]: parseFloat(event.target.value)
    });
  }

}
