import React, { ReactElement } from 'react';
import { WithTranslation, withTranslation } from "react-i18next";
import CustomSelect from './Select';
import jsonData from "../model/data.json";
import NodeEditor from './NodeEditor';
import Trees, { Node, NodeConfig, NodeConfigs } from "../model/Trees";
import ItemRate from './ItemRate';
import RateEditor, { divideIntoGroups } from './RateEditor';
import { ProliferatorEffect } from '../model/Proliferator';
import FlowChart from './FlowChart';
import Facility from '../model/Facility';
import { LoadData, SaveData } from '../util/LocalStorage';
import Item from '../model/Item';


interface Props extends WithTranslation {
}


interface State {
  width: number,
  height: number,
  selectedOptions: { value: string }[],
  nodeConfigs: NodeConfigs,
  perSecond: boolean,
  selectedNodeName?: string, // NOTE: recipe name not item name
}



class Products extends React.Component<Props, State>  {

  private options: { label: string, options: { value: string }[] }[] = [];
  private data: any = jsonData;

  constructor(props: Props) {
    super(props);

    // convert data.catalog to options
    this.options = [];
    for (const groupName in this.data.catalog) {
      let group: { label: string, options: { value: string }[] } = { label: this.props.t(groupName), options: [] };
      for (const i in this.data.catalog[groupName]) {
        let item = this.data.catalog[groupName][i];
        let option: { value: string, label: string } = { value: item, label: this.props.t(item) };
        group.options.push(option);
      }
      this.options.push(group);
    }


    let nodeConfigs = LoadData('nodeConfigs');
    if (nodeConfigs === undefined) {
      nodeConfigs = {};
    }

    let selectedNodeName = LoadData('selectedNodeName');

    let perSecond = LoadData('perSecond');
    if (perSecond === undefined) {
      perSecond = false;
    }

    let selectedOptions = LoadData('selectedOptions');
    if (selectedOptions === undefined) {
      selectedOptions = [{ value: 'Universe matrix' }];
      if (selectedNodeName === undefined) {
        selectedNodeName = 'Universe matrix';
      }
    }

    this.state = {
      width: 1200,
      height: 900,
      selectedOptions: selectedOptions,
      nodeConfigs: nodeConfigs,
      perSecond: perSecond,
      selectedNodeName: selectedNodeName
    };
  }


  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions);
    window.addEventListener("scroll", this.handleScroll);
  }


  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
    window.removeEventListener("scroll", this.handleScroll);
  }


  render() {
    const { selectedOptions: selectedOption } = this.state;

    let trees = [];
    for (let item of selectedOption) {
      trees.push(Trees.Build(item.value, this.state.nodeConfigs));
    }
    const selectedNodes = Trees.GetNodes(trees, this.state.selectedNodeName!);

    let rateEditors = [];
    for (let tree of trees) {
      rateEditors.push(<ItemRate options={[{ value: tree.name }]} />);
    }

    // facilities count
    let facilities = Object.entries(Trees.CountFacilities(trees)).sort((a, b) => {
      // sort by last word
      let aLastWord = a[0].split(' ').slice(-1);
      let bLastWord = b[0].split(' ').slice(-1);
      if (aLastWord < bLastWord) {
        return -1;
      } else if (aLastWord > bLastWord) {
        return 1;
      } else {
        return 0;
      }
    });
    let groupedFacilities = divideIntoGroups(facilities, 2);

    let facilityElements: ReactElement<HTMLDivElement>[] = [];
    for (let [i, row] of groupedFacilities.entries()) {
      let cols: ReactElement<HTMLDivElement>[] = [];
      for (let [facilityName, count] of row) {
        cols.push(
          <div key={facilityName} className="col">
            <ItemRate options={[{ value: facilityName }]} item={{ value: facilityName }}
              rate={count}
            />
          </div>
        );
      }
      facilityElements.push(<div key={i} className={"row"}>{cols}</div>);
    }

    return (
      <div className="container-fluid">
        <div className='mb-3'>
          <CustomSelect
            isMulti={true}
            value={selectedOption}
            options={this.options}
            onChange={this.handleOptionChange}
          />
        </div>
        <div className='row'>
          <div className='col-xl-2' >
            <div className="row">
              <div>
                <div className='row mt-1 mb-2'>
                  <div className='col'>
                    <label className='text-black-50'>{this.props.t('Production')}</label>
                  </div>
                  <div className='col'>
                    <div className="form-check form-switch d-flex justify-content-end">
                      <input className="form-check-input me-2" type="checkbox" role="switch" id="timeSwitchCheck"
                        checked={this.state.perSecond} onChange={e => this.handlePerSecondChange(e.target.checked)}
                      />
                      <label className="form-check-label text-black-50" htmlFor="timeSwitchCheck">
                        <small>{this.props.t(this.state.perSecond ? 'per second' : 'per second')}</small>
                      </label>
                    </div>
                  </div>
                </div>
                <div className='row mt-1'>
                  <RateEditor trees={trees}
                    onRateChanged={this.handleRateChange}
                    numOfCols={this.leftCols(trees.length)}
                    perSecond={this.state.perSecond}
                  />
                </div>
              </div>

              <div className='mt-5'>
                <label className='text-black-50 mb-1'>{this.props.t('Facility')}</label>
                {facilityElements}
              </div>

              <div className='row mt-5'>
                <NodeEditor nodes={selectedNodes}
                  onRecipeChanged={this.handleRecipeChange}
                  onProliferatorChanged={this.handleProliferatorChange}
                  onProliferatorEffectChanged={this.handleProliferatorEffectChange}
                  onFacilityChanged={this.handleFacilityChange}
                  perSecond={this.state.perSecond}
                  onGatheringChanged={this.handleGatheringChange}
                />
              </div>
            </div>
          </div>

          <div className='col-xl-10' >
            <FlowChart width={this.state.width} height={this.state.height}
              trees={trees} onNodeClicked={this.handleNodeClick}
              selectedNode={this.state.selectedNodeName}
              perSecond={this.state.perSecond}
            />
          </div>
        </div>
      </div>
    );
  }


  leftCols = (numOfTrees: number) => {
    return numOfTrees > 20 ? 3 : 2;
  }

  handleScroll = (e: Event) => {
  }

  updateDimensions = () => {
    let [width, height] = this.getFlowchartDimension();
    this.setState({ width: width, height: height });
  }

  setStateWithProperty = (property: string, value: string | number | boolean) => {
    this.setState(
      (prevState) => {
        // TODO 如果selectednodename是item.name，就不用这么麻烦
        let selectedNodeName = prevState.selectedNodeName!;
        // let item = Item.getItemByRecipeName(selectedNodeName)!;
        let nodeConfigs = prevState.nodeConfigs;
        if (!(selectedNodeName in nodeConfigs)) {
          nodeConfigs[selectedNodeName] = {};
        }
        nodeConfigs[selectedNodeName][property] = value;


        return {
          nodeConfigs: nodeConfigs,
        }
      },

      () => {
        SaveData('nodeConfigs', this.state.nodeConfigs);
      }
    );

  }

  handleGatheringChange = (itemName: string, gathering: boolean) => {
    this.setStateWithProperty('gathering', gathering);
  }

  handleProliferatorChange = (proliferatorName: string) => {
    console.log(`proliferator ${proliferatorName}`);
    this.setStateWithProperty('proliferator', proliferatorName);
  }


  handleProliferatorEffectChange = (proliferatorEffect: ProliferatorEffect) => {
    console.log(`proliferator effect ${proliferatorEffect}`);
    this.setStateWithProperty('proliferatorEffect', proliferatorEffect);
  }


  handleFacilityChange = (facilityName: string) => {
    console.log(`facility name ${facilityName}`);
    let facility = Facility.getFacility(facilityName);
    if (!facility) {
      return;
    }
    this.setStateWithProperty('facility', facilityName);
  }


  handleRecipeChange = (recipeName: string) => {
    console.log(`recipe ${recipeName}`);
    this.setStateWithProperty('recipe', recipeName);
  }

  handleRateChange = (recipeName: string, rate: number) => {
    console.log(`${recipeName} ${rate}`);

    if (rate < 0) {
      return;
    }

    let item = Item.getItemByRecipeName(recipeName);
    if (!item) {
      return;
    }

    let nodeConfigs = this.state.nodeConfigs;

    if (!(item.name in nodeConfigs)) {
      nodeConfigs[item.name] = {};
    }
    nodeConfigs[item.name]['rate'] = rate;

    this.setState({ nodeConfigs: nodeConfigs }, () => {
      SaveData('nodeConfigs', this.state.nodeConfigs);
    });
  }

  handleNodeClick = (recipeName: string) => {
    console.log(`click ${recipeName}`);
    this.setState({ selectedNodeName: recipeName }, () => {
      if (this.state.selectedNodeName) {
        SaveData('selectedNodeName', this.state.selectedNodeName)
      }
    });
  }

  handleOptionChange = (selectedOptions: { value: string }[]) => {
    this.setState({ selectedOptions: selectedOptions }, () => {
      SaveData('selectedOptions', this.state.selectedOptions);
    });
  };

  handlePerSecondChange = (checked: boolean) => {
    this.setState({ perSecond: checked }, () => {
      SaveData('perSecond', this.state.perSecond);
    });
  }


  getFlowchartDimension = () => {
    let documentElement = document.documentElement;
    let body = documentElement.getElementsByTagName('body')[0];
    let width = window.innerWidth || document.documentElement.clientWidth || body.clientWidth;
    let height = window.innerHeight || document.documentElement.clientHeight || body.clientHeight;

    width = width / 12 * 10;
    height = height - 155;
    return [width, height];
  }
}


export default withTranslation()(Products);