import * as d3 from "d3";
import * as d3Sankey from "d3-sankey";
import { dominatColor } from "../model/data";
import i18n from "../i18n";

function getDimensions(container, margin) {
  const bbox = container.node().getBoundingClientRect();
  return {
    width: bbox.width - margin.left - margin.right,
    height: bbox.height - margin.top - margin.bottom
  };
}

export default function SankeyChart(element, links, handleNodeClick, width, height, selectedNode, perSecond = true) {
  // const width = 2150;
  // const height = 1460;

  // width = width; /// 12 * 9;
  // height = 70;

  const nodeAlign = d3Sankey.sankeyJustify;
  const nodeWidth = 24;
  const nodePadding = 12;
  const nodeBackground = '#2F4F4F';//'#3c4651';
  const minNodeHeight = nodeWidth;

  const linkStrokeOpacity = .5;
  const linkPath = d3Sankey.sankeyLinkHorizontal();
  const format = (d) => d % 1 === 0 ? d : d.toFixed(2);
  const linkTitle = d => {
    let product = d.product;
    if (product.endsWith('*')) {
      product = product.slice(0, -1);
    }
    return `${i18n.t(product)} * ${format(perSecond ? d.value : d.value * 60)}`;
  };

  const svg = d3.select(element)
    // .attr("width", "100%")
    // .attr("height", "auto")
    .attr("viewBox", [0, 0, width, height])
    .attr("style", "max-width: 100%; height: auto; height: intrinsic;")
    .html(''); // TODO 不清除，用exit和enter处理

  if (links.length === 0) {
    return;
  }

  // const {width, height} = getDimensions(svg, 0);

  // Derive nodes from links.
  let nodes = Array.from(
    links.reduce(function (acc, link) {
      // let source = { item: link.source, recipe: link.sourceRecipe };

      if (!acc.some(x => x[0] === link.source)) {
        acc.push([link.source, link.sourceRecipe]);
      }

      // let target = { item: link.target, recipe: link.targetRecipe };
      if (!acc.some(x => x[0] === link.target)) {
        acc.push([link.target, link.targetRecipe]);
      }

      return acc;
    }, []),

    pair => ({ id: pair[0], recipe: pair[1] })
  );

  let sankeyData = { nodes: nodes, links: links };

  // Compute the Sankey layout.
  let sankey = d3Sankey.sankey()
    .nodeId((d) => d.id)
    .nodeAlign(nodeAlign)
    .nodeWidth(nodeWidth)
    .nodePadding(nodePadding)
    .size([width, height]);
  // ({ nodes, links });
  let graph = sankey(sankeyData);

  // sankey.update(graph);

  // console.log(graph);
  // links
  const link = svg.append("g")
    .attr("fill", "none")
    .attr("stroke-opacity", linkStrokeOpacity)
    .selectAll("g")
    .data(graph.links)
    .join("g")
    .style("mix-blend-mode", 'multiply');

  link.append("path")
    .attr("d", linkPath)
    .attr("stroke", (d) => {
      let product = d.product;
      if (product.endsWith('*')) {
        product = product.slice(0, -1);
      }
      return dominatColor[product];
    })
    .attr("stroke-width", (d) => Math.max(1, d.width))
    .append("title")
    .text(linkTitle);

  // link.each(function (d) {
  //   d3.select(this).on("click", () => {
  //     handleLinkClick(d.source.id, d.target.id, d.product);
  //   })
  // });


  // nodes
  const node = svg.append("g")
    .selectAll("g")
    .data(graph.nodes)
    .join("g")
    .attr("transform", d => {
      // make node stroke align center of node
      let h = d.y1 - d.y0;
      let y0 = d.y0;
      if (h < minNodeHeight) {
        y0 -= (minNodeHeight - h) / 2;
      }
      return `translate(${d.x0}, ${d.y0})`
    });

  let getNodeWidth = (d) => d.x1 - d.x0;
  let getNodeHeight = (d) => {
    let h = d.y1 - d.y0;
    return h < minNodeHeight ? minNodeHeight : h;
  };

  node.append("rect")
    .attr("width", getNodeWidth)
    .attr("height", getNodeHeight)
    .attr("stroke-width", 0)
    .attr("fill", d => {
      if (d.id === selectedNode) {
        return '#000';//'#BF360C';
      } else {
        return nodeBackground;
      }
    });
  // .attr("fill", nodeBackground);

  node.append("image")
    .attr("xlink:href", d => {
      let name = d.recipe;
      if (name === undefined) {
        name = d.id;
      }
      if (name.endsWith('*')) {
        name = name.slice(0, -1);
      }
      return `/icons/${name}.png`;
    })
    .attr("width", getNodeWidth)
    .attr("height", getNodeHeight);

  node.append("title").text(d => i18n.t(d.recipe ? d.recipe : d.id));

  // 这种方式无法获取d.id，此时的d是一个event
  // node.on("click", (d) => {
  //   console.log(`click on ${d}`);
  // });

  node.each(function (d) {
    d3.select(this).on("click", () => {
      handleNodeClick(d.id)
    });
    // d3.select(this).on("drag", dragmove);
  });



}

