import { withTranslation } from 'react-i18next';
import DysonSphere from "./DysonSphere";


class DysonSwarm extends DysonSphere {

  render() {
    // 对于戴森云，每个太阳帆的功率是36kw
    let dysonSwarmGeneration = this.state['solarSailProduction'] / this.state['solarSailProductionTimeUnit'] *
      0.036 * this.state['solarSailLife'] * this.state['starLumination'];
    if (isNaN(dysonSwarmGeneration)) {
      dysonSwarmGeneration = 0;
    }
    let dysonSwarmGenerationStr = dysonSwarmGeneration > 1000 ?
      `${(dysonSwarmGeneration / 1000).toFixed(2)} GW` :
      `${dysonSwarmGeneration.toFixed(2)} MW`;

    return (
      <div className="container">
        <form>
          {/* star lumination */}
          <div className="mt-5 mb-3">
            <label htmlFor="starLuminationInput" className="form-label">{this.props.t('Star Lumination')}</label>
            <input type="number" className="form-control" id="starLuminationInput"
              name="starLumination" value={this.state['starLumination']} onChange={this.handleChange} />
          </div>
          {/* solar sail production */}
          <div className="mt-3">
            <label htmlFor="solarSailProductionInput" className="form-label">
              {this.props.t("Solar Sail Production")}
            </label>
            <div className="input-group">
              <input type="number" className="form-control w-50" id="solarSailProductionInput"
                name="solarSailProduction" value={this.state['solarSailProduction']} onChange={this.handleChange} />
              {/* per unit time */}
              <select className="form-select w-25" name="solarSailProductionTimeUnit"
                defaultValue={this.state['solarSailProductionTimeUnit']}
                onChange={this.handleChange}>
                <option value="1">{this.props.t("per second")}</option>
                <option value="60">{this.props.t("per miniute")}</option>
              </select>
            </div>
          </div>
          {/* solar sail life */}
          <div className="mt-3 mb-3">
            <label htmlFor="solarSailLifeInput" className="form-label">{this.props.t('Solar Sail Life')}</label>
            <div className="input-group">
              <input type="number" className="form-control" id="solarSailLifeInput"
                name="solarSailLife" value={this.state['solarSailLife']} onChange={this.handleChange} />
              <span className="input-group-text" id="inputGroupPrepend">{this.props.t("seconds")}</span>
            </div>
          </div>
          {/* dyson swarm generation */}
          <div className="mt-3 mb-3">
            <label htmlFor="dysonSwarmGenerationInput" className="form-label">
              {this.props.t('Generation of Dyson Swarm')}
            </label>
            <input type="text" className="form-control" id="dysonSwarmGenerationInput" disabled
              name="dysonSwarmGeneration" value={dysonSwarmGenerationStr} />
          </div>
        </form>
      </div>
    );
  }
}

export default withTranslation()(DysonSwarm);