import React, { ReactElement } from "react";
import { WithTranslation, withTranslation } from 'react-i18next';
import { Node } from "../model/Trees";
import CustomSelect from "./Select";
import ItemRate from "./ItemRate";
import Proliferator, { ProliferatorEffect } from "../model/Proliferator";
import Facility from "../model/Facility";
import { format, addLabelToOption, addLabelToOptions } from "../util/util";


interface Props extends WithTranslation {
  nodes: Node[];
  onRecipeChanged: (recipeName: string) => void
  onProliferatorChanged: (recipeName: string) => void
  onProliferatorEffectChanged: (effect: ProliferatorEffect) => void
  onFacilityChanged: (facilityName: string) => void
  onGatheringChanged: (itemName: string, gathering: boolean) => void
  perSecond: boolean
}


class NodeEditor extends React.Component<Props>  {

  render() {
    let nodes = this.props.nodes;

    if (nodes.length === 0) {
      return <div></div>;
    }

    if(nodes[0].recipeName === 'X-ray cracking') {
      console.log(nodes.length);
      for(let node of nodes) {
        console.log(node.producedByProduct);
      }
    }

    let elements: ReactElement<HTMLDivElement>[] = [];

    let productName = nodes[0].item.name;
    let rate = 0;
    let numOfFacilities = 0;
    for (let node of nodes) {
      rate += node.rate;
      numOfFacilities += node.numOfFacilities;
    }

    let productLabel = this.props.t('Product');
    if (!nodes[0].recipe) {
      productLabel = this.props.t('Natural Resource');
    }

    // gathering
    let item = nodes[0].item;
    elements.push(
      <div key={"product"} className='mb-1'>
        <label className="form-label text-black-50">
          {productLabel}
        </label>
        <ItemRate options={[{ value: productName }]} item={{ value: productName }}
          denominator={numOfFacilities} rate={this.props.perSecond ? rate : rate * 60}
        />

        {item.gatheredBy.length > 0 && item.recipes.length > 0 &&
          <div className="form-check form-switch d-flex justify-content-end">
            <input className="form-check-input me-2" type="checkbox" role="switch" id="gatheringSwitchCheck"
              checked={nodes[0].recipe === undefined}
              onChange={e => this.props.onGatheringChanged(item.name, e.target.checked)}
            />
            <label className="form-check-label text-black-50" htmlFor="gatheringSwitchCheck">
              <small>{this.props.t('gathering')}</small>
            </label>
          </div>
        }
      </div>
    );


    if (nodes[0].recipe) {
      let recipe = nodes[0].recipe;
      let proliferator = nodes[0].proliferator;

      // num of proliferators
      let numOfProliferators = 0;
      // total rate
      // by-product rate
      let byProductRate = 0;
      let usedByProductRate = 0;
      let totalMaterials: { [key: string]: number } = {};
      let selfSupplyMaterials: { [key: string]: number } = {};

      for (let node of nodes) {
        numOfProliferators += node.numOfProliferators;

        let producedByProduct = node.producedByProduct;
        if (producedByProduct) {
          byProductRate += producedByProduct.rate;
          if (node.isByProductUsed) {
            usedByProductRate += producedByProduct.rate;
          }
        }

        for (let [material, rate] of Object.entries(node.getMaterials()!)) {
          // console.log(material);
          if (material in totalMaterials) {
            totalMaterials[material] += rate;
          } else {
            totalMaterials[material] = rate;
          }
        }

        let selfSupply = node.getSelfSupplyMaterials();
        if (selfSupply) {
          if (selfSupply.name in selfSupplyMaterials) {
            selfSupplyMaterials[selfSupply.name] += selfSupply.rate;
          } else {
            selfSupplyMaterials[selfSupply.name] = selfSupply.rate;
          }
        }
      }


      // recipes
      let recipeOptions = [];
      let recipeOption = undefined;
      if (nodes[0].recipe.name === 'Reforming refine' || nodes[0].recipe.name === 'Plasma refining') {
        recipeOptions = [{ value: nodes[0].recipe.name }];
        recipeOption = { value: nodes[0].recipe.name };
      } else {
        for (let recipe of nodes[0].item.recipes) {
          let option = { value: recipe.name };
          recipeOptions.push(option);
          if (recipe.name === nodes[0].recipeName) {
            recipeOption = option;
          }
        }
      }
      // facility
      let facilityType = nodes[0].facilityType!;
      let facilityOptions = Facility.getFacilities(facilityType).map((x: Facility) => {
        return { value: x.name };
      });
      let facility = nodes[0].facility!;
      let facilityOption = { value: facility.name };


      elements.push(
        <div key={"recipe"} className='mb-3'>
          {/* <label className="form-label text-black-50">配方</label> */}
          <div className="input-group">
            <CustomSelect className="react-select-start" classNamePrefix="react-select-start" onlyIcon={true}
              options={addLabelToOptions(recipeOptions)} value={addLabelToOption(recipeOption!)}
              onChange={(option: { value: string }) => this.props.onRecipeChanged(option.value)}
            />
            <CustomSelect className="react-select-middle" classNamePrefix="react-select-middle" onlyIcon={true}
              options={addLabelToOptions(facilityOptions)} value={addLabelToOption(facilityOption!)}
              onChange={(option: { value: string }) => this.props.onFacilityChanged(option.value)}
            />
            <input className="form-control text-center" type="number"
              readOnly={true} value={format(numOfFacilities, 2)}
            />
          </div>
        </div>
      );


      // proliferator
      let proliferatorOptions: { value: string }[] = [];
      for (let proliferator of Proliferator.Values) {
        proliferatorOptions.push({ value: proliferator.name });
      }

      let proliferatorEffectOptions: { value: string }[] = [];
      Object.values(ProliferatorEffect).forEach(x => {
        if (!(!recipe.extraProducts && x === ProliferatorEffect.ExtraProducts)) {
          proliferatorEffectOptions.push({ value: x });
        }
      });
      let proliferatorEffectOption = { value: nodes[0].proliferatorEffect };

      elements.push(
        <div className="mb-3">
          <label className="form-label text-black-50">{this.props.t('Proliferator')}</label>
          <div className="mb-1">
            <CustomSelect className="react-select-start" classNamePrefix="react-select-start" onlyText={true}
              options={addLabelToOptions(proliferatorEffectOptions)} value={addLabelToOption(proliferatorEffectOption!)}
              onChange={(option: { value: string }) => this.props.onProliferatorEffectChanged(option.value as ProliferatorEffect)}
            />
          </div>
          <div key={"proliferator"} className='mb-2'>
            <div className="input-group">
              <CustomSelect className="react-select-middle" classNamePrefix="react-select-middle" onlyIcon={true}
                options={addLabelToOptions(proliferatorOptions)} value={addLabelToOption({ value: proliferator.name })}
                onChange={(option: { value: string }) => this.props.onProliferatorChanged(option.value)}
              />
              <input className="form-control text-center" type="number"
                readOnly={true} value={format(this.props.perSecond ? numOfProliferators : numOfProliferators * 60, 2)}
              />
            </div>
          </div>
        </div>
      );

      // materials
      let materialElements: ReactElement<HTMLDivElement>[] = [];
      for (let [material, rate] of Object.entries(totalMaterials)) {
        materialElements.push(
          <ItemRate key={material} options={[{ value: material }]} item={{ value: material }}
            denominator={numOfFacilities} rate={this.props.perSecond ? rate : rate * 60}
          />
        );
      }

      elements.push(
        <div key={"materials"} className="mt-3">
          <label className="form-label text-black-50">{this.props.t('Material')}</label>
          {materialElements}
        </div>
      );

      // self supply
      if (Object.keys(selfSupplyMaterials).length > 0) {
        let selfSupplyMaterialElements: ReactElement<HTMLDivElement>[] = [];
        for (let [material, rate] of Object.entries(selfSupplyMaterials)) {
          selfSupplyMaterialElements.push(
            <ItemRate key={material} options={[{ value: material }]} item={{ value: material }}
              denominator={numOfFacilities} rate={this.props.perSecond ? rate : rate * 60}
            />
          );
        }

        elements.push(
          <div key={"selfsupplymaterials"} className="mt-3">
            <label className="form-label text-black-50">{this.props.t('Self-supply')} <small>({this.props.t("use this part of the product as material")})</small></label>
            {selfSupplyMaterialElements}
          </div>
        );
      }

      // by-products
      let byProductMsg = '';
      if (byProductRate === usedByProductRate) {
        byProductMsg = this.props.t('used');
      } else if (usedByProductRate > 0) {
        byProductMsg = `${this.props.t('partial-used')} ${format(this.props.perSecond ? usedByProductRate : usedByProductRate * 60, 2)}`;
      } else {
        byProductMsg = this.props.t('unused');
      }
      let byProductName = recipe.byProduct?.name;
      if (byProductName) {
        elements.push(
          <div key={"by-product"} className='mt-2'>
            <label className="form-label text-black-50">{this.props.t('By-product')} <small>{`(${byProductMsg})`}</small></label>
            <ItemRate options={[{ value: byProductName }]} item={{ value: byProductName }}
              denominator={numOfFacilities} rate={this.props.perSecond ? byProductRate : byProductRate * 60}
            />
          </div>
        );
      }

    }

    return (
      <div>{elements}</div>
    );
  }
}


export default withTranslation()(NodeEditor);