import React, { ReactElement } from "react";
import { WithTranslation, withTranslation } from "react-i18next";
import CustomSelect from "./Select";
import { format, addLabelToOption, addLabelToOptions } from "../util/util";
import * as LocalStorage from '../util/LocalStorage'
import data from "../model/data.json";


interface Props extends WithTranslation {
}

interface State {
  // dyson sphere
  starLumination: number,
  solarSailProduction: number,
  solarSailProductionTimeUnit: number,
  solarSailLife: number,
  structurePoint: number,
  cellPoint: number,
  nodes: number,
  adsorptionTimeUnit: number,

  // others
  windEnergyRatio: number,
  solarEnergyRatio: number,

  fuelTimeUnit: number,
  powerFacilities: { [key: string]: number },
  selectedPowerFacility: string,
  fuel: { [key: string]: { name: string, rate: number } },
};



class PowerPage extends React.Component<Props, State>{
  private static readonly DATA_KEY = "power";

  constructor(props: Props) {
    super(props);

    let data = LocalStorage.LoadData(PowerPage.DATA_KEY);
    if (data === undefined) {
      data = {
        starLumination: 1,
        solarSailProduction: 0,
        solarSailProductionTimeUnit: 1,
        solarSailLife: 3600,
        structurePoint: 0,
        cellPoint: 0,
        nodes: 0,
        adsorptionTimeUnit: 1,

        windEnergyRatio: 1,
        solarEnergyRatio: 1,
        fuelTimeUnit: 1,
        selectedPowerFacility: "Thermal power plant",
        powerFacilities: {
          "Solar panel": 10,
          "Wind turbine": 10,
        },
        fuel: {
          "Thermal power plant": { name: "Coal", rate: 1 },
          "Mini fusion power plant": { name: "Deuteron fuel rod", rate: 1 },
          "Artificial star": { name: "Antimatter fuel rod", rate: 1 },
        },
      };
    }
    this.state = data;
  }


  render() {
    let powerFacilityOptions: { value: string }[] = [];
    for (let facility of Object.keys(data.power.facility)) {
      powerFacilityOptions.push({ value: facility });
    }
    let selectedPowerFacilityOption = { value: this.state.selectedPowerFacility };

    let needFuel = this.state.selectedPowerFacility !== 'Solar panel' && this.state.selectedPowerFacility !== 'Wind turbine';

    let numOfFacilities = 0;
    let fuelGeneration = 0;
    let fuelElement: ReactElement<HTMLDivElement> | undefined = undefined;
    if (needFuel) {
      let fuelOptions: { value: string }[] = [];
      // @ts-ignore
      let fuels: string[] = Object.values(data.power.facility[this.state.selectedPowerFacility].fuel);
      for (let fuel of fuels) {
        fuelOptions.push({ value: fuel });
      }

      // @ts-ignore
      let powerPlant: { power: number, efficiency: number, fuel: string[] } = data.power.facility[this.state.selectedPowerFacility];
      let fuelName = this.state.fuel[this.state.selectedPowerFacility].name;
      let fuelRate = this.state.fuel[this.state.selectedPowerFacility].rate;
      let fuelOption: { value: string } = { value: fuelName };
      // @ts-ignore
      fuelGeneration = data.power.fuel[fuelName] * fuelRate * powerPlant.efficiency;
      // @ts-ignore
      numOfFacilities = fuelGeneration / powerPlant.power;

      fuelElement = (
        <div className="mt-3 mb-3">
          <label htmlFor="otherGenerationInput" className="form-label text-black-50">
            {this.props.t('Fuel')}
          </label>
          <div className="input-group">
            <div style={{ width: 146 }}>
              <CustomSelect className="react-select-start" classNamePrefix="react-select-start" onlyIcon={true}
                options={addLabelToOptions(fuelOptions)} value={addLabelToOption(fuelOption)}
                onChange={(option: { value: string }) => this.handleFuelNameChange(option.value)}
              />
            </div>
            <input className="form-control" type="number"
              value={format(fuelRate * this.state.fuelTimeUnit, 2)} onChange={(e) => this.handleFuelRateChange(parseFloat(e.target.value))}
            />
            {/* per unit time */}
            <select className="form-select"
              name="fuelTimeUnit"
              defaultValue={this.state.fuelTimeUnit}
              onChange={this.handlePropertyChange}
            >
              <option value="1">{this.props.t("per second")}</option>
              <option value="60">{this.props.t("per miniute")}</option>
            </select>
          </div>
        </div>
      );
    } else {
      numOfFacilities = this.state.powerFacilities[this.state.selectedPowerFacility];
      // @ts-ignore
      let energy: number = data.power.facility[this.state.selectedPowerFacility].power;
      fuelGeneration = energy * numOfFacilities;
      if (this.state.selectedPowerFacility === 'Solar panel') {
        fuelGeneration *= this.state.solarEnergyRatio;
      } else {
        fuelGeneration *= this.state.windEnergyRatio;
      }
    }

    let fuelGenerationStr = this.formatStr(fuelGeneration);

    // 太阳帆吸附速度：一个结点每秒0.5个
    let solarSailAdsorption = 0.5 * this.state.nodes * this.state.adsorptionTimeUnit;
    if (isNaN(solarSailAdsorption)) {
      solarSailAdsorption = 0;
    }

    // TODO 功率的计算仍有问题，单个细胞的功率具体是多少，15kw太大，是14.9kw吗？
    let dysonShellGeneration = (this.state.structurePoint * 0.096 + this.state.cellPoint * 0.0149) *
      this.state['starLumination'];
    let dysonShellGenerationStr = this.formatStr(dysonShellGeneration);

    // 对于戴森云，每个太阳帆的功率是36kw
    let dysonSwarmGeneration = this.state.solarSailProduction / this.state.solarSailProductionTimeUnit *
      0.036 * this.state.solarSailLife * this.state.starLumination;
    let dysonSwarmGenerationStr = this.formatStr(dysonSwarmGeneration);


    return (
      <div className="container">
        <form>
          <div className="p-3 border border-light rounded mb-5" style={{ background: "#fafafa" }}>
            <fieldset>
              <div className="mb-3">
                <label htmlFor="otherGenerationInput" className="form-label text-black-50">
                  {this.props.t('Power Facility')}
                </label>
                <div className="input-group">
                  <div style={{ width: 146 }}>
                    <CustomSelect className="react-select-start" classNamePrefix="react-select-start" onlyIcon={true}
                      options={addLabelToOptions(powerFacilityOptions)} value={addLabelToOption(selectedPowerFacilityOption)}
                      onChange={(option: { value: string }) => this.handlePowerFacilityChange(option.value)}
                    />
                  </div>
                  <input className="form-control" type="number" readOnly={needFuel}
                    value={format(numOfFacilities, 1)} onChange={(e) => {
                      let rate = 0;
                      if (e.target.value !== null) {
                        rate = parseFloat(e.target.value);
                      }
                      this.handleFacilityRateChange(rate);
                    }}
                  />
                </div>
              </div>

              {this.state.selectedPowerFacility === 'Wind turbine' &&
                <div className="mt-2">
                  <label htmlFor="starLuminationInput" className="form-label text-black-50">{'风能利用率'}</label>
                  <div className="input-group">
                    <input type="number" className="form-control" id="windEnergyRatioInput"
                      name="windEnergyRatio" value={this.state.windEnergyRatio * 100}
                      onChange={(e) => { this.handleEnergyRatioChange('windEnergyRatio', parseFloat(e.target.value)) }}
                    />
                    <span className="input-group-text">{'%'}</span>
                  </div>
                </div>
              }

              {this.state.selectedPowerFacility === 'Solar panel' &&
                <div className="mt-2">
                  <label htmlFor="starLuminationInput" className="form-label text-black-50">{'光能利用率'}</label>
                  <div className="input-group">
                    <input type="number" className="form-control" id="solarEnergyRatioInput"
                      name="solarEnergyRatio" value={this.state.solarEnergyRatio * 100}
                      onChange={(e) => { this.handleEnergyRatioChange('solarEnergyRatio', parseFloat(e.target.value)) }}
                    />
                    <span className="input-group-text">{'%'}</span>
                  </div>
                </div>
              }

              {needFuel &&
                fuelElement
              }
              <div className="mt-3">
                <label htmlFor="otherGenerationInput" className="form-label text-black-50">
                  {this.props.t('Generation capacity')}
                </label>
                <input type="text" className="form-control" id="otherGenerationInput" disabled
                  name="otherGeneration" value={fuelGenerationStr} />
              </div>

            </fieldset>
          </div>


          <div className="p-3 border border-light rounded mb-4" style={{ background: "#fafafa" }}>
            <fieldset>
              {/* star lumination */}
              <div className="mt-2 mb-4">
                <label htmlFor="starLuminationInput" className="form-label text-black-50">{this.props.t('Star Lumination')}</label>
                <input type="number" className="form-control" id="starLuminationInput"
                  name="starLumination" value={this.state['starLumination']}
                  onChange={this.handlePropertyChange}
                />
              </div>
            </fieldset>

            <fieldset>
              {/* solar sail production */}
              <div className="mt-3">
                <label htmlFor="solarSailProductionInput" className="form-label text-black-50">
                  {this.props.t("Solar Sail Production")}
                </label>
                <div className="input-group">
                  <input type="number" className="form-control w-50" id="solarSailProductionInput"
                    name="solarSailProduction" value={this.state['solarSailProduction']}
                    onChange={this.handlePropertyChange}
                  />
                  {/* per unit time */}
                  <select className="form-select w-25" name="solarSailProductionTimeUnit"
                    defaultValue={this.state['solarSailProductionTimeUnit']}
                    onChange={this.handlePropertyChange}>
                    <option value="1">{this.props.t("per second")}</option>
                    <option value="60">{this.props.t("per miniute")}</option>
                  </select>
                </div>
              </div>
              {/* solar sail life */}
              <div className="mt-3 mb-3">
                <label htmlFor="solarSailLifeInput" className="form-label text-black-50">{this.props.t('Solar Sail Life')}</label>
                <div className="input-group">
                  <input type="number" className="form-control" id="solarSailLifeInput"
                    name="solarSailLife" value={this.state['solarSailLife']} onChange={this.handlePropertyChange} />
                  <span className="input-group-text" id="inputGroupPrepend">{this.props.t("seconds")}</span>
                </div>
              </div>
              {/* dyson swarm generation */}
              <div className="mt-3 mb-4">
                <label htmlFor="dysonSwarmGenerationInput" className="form-label text-black-50">
                  {this.props.t('Generation of Dyson Swarm')}
                </label>
                <input type="text" className="form-control" id="dysonSwarmGenerationInput" disabled
                  name="dysonSwarmGeneration" value={dysonSwarmGenerationStr} />
              </div>
            </fieldset>

            <fieldset>
              {/* structure point */}
              <div className="mt-3">
                <label htmlFor="structurePointInput" className="form-label text-black-50">{this.props.t('Structure Point')}</label>
                <input type="number" className="form-control" id="structurePointInput"
                  name="structurePoint" value={this.state['structurePoint']} onChange={this.handlePropertyChange} />
              </div>
              {/* cell point */}
              <div className="mt-3">
                <label htmlFor="cellPointInput" className="form-label text-black-50">{this.props.t('Cell Point')}</label>
                <input type="number" className="form-control" id="cellPointInput"
                  name="cellPoint" value={this.state['cellPoint']} onChange={this.handlePropertyChange} />
              </div>
              {/* dyson shell generation */}
              <div className="mt-3 mb-3">
                <label htmlFor="dysonShellGenerationInput" className="form-label text-black-50">
                  {this.props.t('Generation of Dyson Shell')}
                </label>
                <input type="text" className="form-control" id="dysonShellGenerationInput" disabled
                  value={dysonShellGenerationStr} onChange={this.handlePropertyChange} />
              </div>

              {/* nodes */}
              <div className="mt-4">
                <label htmlFor="nodesInput" className="form-label text-black-50">{this.props.t('Nodes')}</label>
                <input type="number" className="form-control" id="nodesInput"
                  name="nodes" value={this.state['nodes']} onChange={this.handlePropertyChange} />
              </div>
              {/* solar sail adsorption */}
              <div className="mt-3">
                <label htmlFor="solarSailAdsorptionInput" className="form-label text-black-50">
                  {this.props.t('Solar Sail Adsorption')}
                </label>
                <div className="input-group">
                  <input type="number" className="form-control w-50" id="solarSailAdsorptionInput" disabled
                    value={solarSailAdsorption} />
                  {/* per unit time */}
                  <select className="form-select w-25"
                    name="adsorptionTimeUnit"
                    defaultValue={this.state.adsorptionTimeUnit}
                    onChange={this.handlePropertyChange}
                  >
                    <option value="1">{this.props.t("per second")}</option>
                    <option value="60">{this.props.t("per miniute")}</option>
                  </select>
                </div>
              </div>
            </fieldset>
          </div>

        </form>
      </div>
    );
  }

  handlePropertyChange = (e: React.FormEvent<HTMLInputElement | HTMLSelectElement>) => {
    this.setState<never>({
      [e.currentTarget.name]: parseFloat(e.currentTarget.value)
    }, () => {
      LocalStorage.SaveData(PowerPage.DATA_KEY, this.state);
    });
  }

  handlePowerFacilityChange = (facility: string) => {
    this.setState({
      selectedPowerFacility: facility
    }, () => {
      LocalStorage.SaveData(PowerPage.DATA_KEY, this.state);
    });
  }

  handleFuelNameChange = (fuelName: string) => {
    let fuel = this.state.fuel;
    fuel[this.state.selectedPowerFacility].name = fuelName;
    this.setState({
      fuel: fuel
    }, () => {
      LocalStorage.SaveData(PowerPage.DATA_KEY, this.state);
    });
  }

  handleFuelRateChange = (fuelRate: number) => {
    let fuel = this.state.fuel;
    fuel[this.state.selectedPowerFacility].rate = fuelRate / this.state.fuelTimeUnit;
    this.setState({
      fuel: fuel
    }, () => {
      LocalStorage.SaveData(PowerPage.DATA_KEY, this.state);
    });
  }

  handleEnergyRatioChange = (name: string, ratio: number) => {
    if (!(name === 'windEnergyRatio' || name === 'solarEnergyRatio')) {
      return;
    }
    if (ratio === NaN || ratio < 0) {
      return;
    }

    this.setState<never>({ [name]: ratio / 100 }, () => {
      LocalStorage.SaveData(PowerPage.DATA_KEY, this.state);
    });
  }

  handleFacilityRateChange = (rate: number) => {
    if (rate < 0 || rate === NaN) {
      return;
    }

    let powerFacilities = this.state.powerFacilities;
    powerFacilities[this.state.selectedPowerFacility] = rate;
    this.setState({
      powerFacilities: powerFacilities
    }, () => {
      LocalStorage.SaveData(PowerPage.DATA_KEY, this.state);
    });
  }

  formatStr = (value: number) => {
    if (value === NaN) {
      value = 0;
    }
    return value > 1000 ? `${(value / 1000).toFixed(2)} GW` : `${value.toFixed(2)} MW`;
  }

}

export default withTranslation()(PowerPage);