import i18n from "../i18n";

export let format = (num: number, decimalPlaces: number) => {
  if (num === undefined || num === null) {
    return '';
  } else if (num % 1 === 0) {
    return num.toString();
  } else {
    let str = num.toFixed(decimalPlaces);
    // 清除末尾的0，否则在有小数点时，输入后面的数字会很困难
    while (str.length > 0 && str.slice(-1) === '0') {
      str = str.slice(0, -1);
    }
    // remove suffix '.'
    if (str.slice(-1) === '.') {
      str = str.slice(0, -1);
    }
    return str;
  }
}

export let addLabelToOption = (option: { value: string }): { value: string, label: string } => {
  return { value: option.value, label: i18n.t(option.value) };
}

export let addLabelToOptions = (options: { value: string }[]): { value: string, label: string }[] => {
  return options.map(x => { return addLabelToOption(x); });
}
