import { FacilityType } from "./Facility";


export default class Recipe {
  public constructor(
    private _name: string,
    private _madeIn: FacilityType,
    private _materials: { [key: string]: number },
    private _time: number,
    private _product: { name: string, quantity: number },
    private _byProduct?: { name: string, quantity: number },
    // 是否能增产
    private _extraProducts = true,

    // 这两个选项主要是X射线裂解使用
    private _selfSupply?: { name: string, quantity: number },
    private _unproducibleMaterials: string[] = [],
    // 这个选项主要是能量矩阵使用
    private _hatedByProducts: string[] = [],
    // 为material指定recipe
    private _materialRecipe: { [key: string]: string } = {}
  ) { }


  // getters and setters
  public get name(): string {
    return this._name;
  }

  public get madeIn(): FacilityType {
    return this._madeIn;
  }

  public get materials(): { [key: string]: number } {
    return this._materials;
  }

  public get time(): number {
    return this._time;
  }

  public get product(): { name: string, quantity: number } {
    return this._product;
  }

  public get byProduct(): { name: string, quantity: number } | undefined {
    return this._byProduct;
  }

  public get extraProducts(): boolean {
    return this._extraProducts;
  }

  public get selfSupply(): { name: string, quantity: number } | undefined {
    return this._selfSupply;
  }

  public getMaterialRecipe(name: string): string | undefined {
    return this._materialRecipe[name];
  }

  public doYouHateByProduct(name: string): boolean {
    // NOTE 用includes，不要用in，in应该只是判断object的key，对数组无效
    return this._hatedByProducts.includes(name);
  }

  public isUnproducibleMaterial(name: string): boolean {
    return this._unproducibleMaterials.includes(name);
  }


}