// 设置一个版本号，每次读取数据时，要对比版本号，如果不一致，清除localstorage。
// 避免在数据不兼容的情况下造成bug，而这种bug使用者是很难修复的。
// 当存储的数据结构发生改变时，要修改version。
let version = "0.0.1"

export function SaveData(key: string, data: any) {
  if (data === undefined) {
    return;
  }

  let str = JSON.stringify(data);
  window.localStorage.setItem(key, str);
  // save data version
  window.localStorage.setItem("version", version);
}

export function LoadData(key: string) {
  let oldVersion = window.localStorage.getItem("version");
  if (oldVersion && oldVersion !== version) {
    // 数据版本不兼容
    window.localStorage.clear();
    return undefined;
  }

  let str = window.localStorage.getItem(key);
  if (!str) {
    return undefined;
  } else {
    return JSON.parse(str);
  }
}