import data from "./data.json";


enum Type {
  SmeltingFacility = 'Smelting Facility',
  Assembler = "Assembler",
  ChemicalFacility = 'Chemical Facility',
  ParticleCollider = 'Particle Collider',
  RefiningFacility = 'Refining Facility',
  ResearchFacility = 'Research Facility',
  EnergyExchanger = 'Energy exchanger',
  MiningFacility = 'Mining Facility',
  FluidPumpingFacility = 'Fluid Pumping Facility',
  OrbitalCollector = 'Orbital Collector',
  OilExtractionFacility = 'Oil Extraction Facility'
}

export { Type as FacilityType };


export default class Facility {
  private constructor(
    private _name: string,
    private _type: Type,
    private _productionSpeed: number,
    private _workConsumption: number // kw
  ) { }

  public get name(): string {
    return this._name;
  }

  public get type(): Type {
    return this._type;
  }

  public get productionSpeed(): number {
    return this._productionSpeed;
  }

  public get workConsumption(): number {
    return this._workConsumption;
  }

  public static getFacilities(type: Type): Facility[] {
    return this._Values.filter(x => x.type === type);
  }

  public static getFacility(name: string): Facility | undefined {
    for (let facility of this._Values) {
      if (facility.name === name) {
        return facility;
      }
    }

    return undefined;
  }

  private static readonly _Values = data.facility.map(
    (value) => {
      return new Facility(
        value.name,
        value.type as Type,
        value.productionSpeed,
        value.workConsumption
      );
    }
  );
}